html {
  min-height: 100%;
  top: 0;
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  scroll-behavior: smooth;

  background: -webkit-linear-gradient(45deg, #8a8a8a, #333333) no-repeat center;

  background: -o-linear-gradient(45deg, #8a8a8a, #333333) no-repeat center;

  background: linear-gradient(45deg, #8a8a8a, #333333) no-repeat center;
  background-size: cover;


}



