.navigation-container {
    margin-top: 20px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: center;
    width: 100%;
}

.menu-list {
    -webkit-columns: 1;
            columns: 1;
    -webkit-column-width: auto;
            column-width: auto;
    letter-spacing: 5px;
}

.navigation-item, .menu-button {
    margin-bottom: 5px;
    list-style: none;
    border: 1px solid black;
    border-radius: 0 16px 0 16px;

    padding: 20px 0;

    opacity: 0.3;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.navigation-item:hover, .menu-button:hover {
    opacity: 1;
    background: rgba(255,255,255,0.4);
    color: black;
    letter-spacing: 2px;
    border: 1px solid rgba(255,255,255,0);
    border-radius: 16px 0 16px 0;
}


